<template>
	<view class="to-delivery-page">
		
		<!-- 选择地址 -->
		<order-address-picker :order="formData" class="order-address-picker">
			<template #start-point>
				<text>地址簿</text>
			</template>
			<template #end-point>
				<text>地址簿</text>
			</template>
		</order-address-picker>
		
		<!-- 货物信息 -->
		<view class="form-block">
			<form-item arrow @click.native="onChooseGoods">
				<template #label>
					<view>
						<text>货物信息</text>
						<text class="required">必填</text>
					</view>
				</template>
				<input class="goods-picker-input" disabled type="text" v-model="formData.goodsName" placeholder="请选择" placeholder-class="placeholder" />
			</form-item>
		</view>
		
		<view class="form-block">
			<form-item label="重量（吨）" class="form-item gap-form-item">
				<input class="number-input" type="text" v-model="formData.weight" placeholder="约0-999" placeholder-class="placeholder" />
			</form-item>
			<form-item label="体积（方）" class="form-item gap-form-item">
				<input class="number-input" type="text" v-model="formData.volumn" placeholder="约0-999" placeholder-class="placeholder" />
			</form-item>
		</view>
		
		<view class="form-block">
			<form-item label="数量" class="form-item">
				<number-step v-model="formData.number"></number-step>
			</form-item>
		</view>
		
		<view class="form-block padding-bottom-zero">
			<form-item label="货物照片" class="form-item" label-position="top">
				<img-upload class="img-upload" v-model="formData.img" mult></img-upload>
			</form-item>
		</view>
		
		<view class="form-block padding-bottom-zero">
			<view class="switch-items">
				<form-item label="上门取件" class="switch-form-item">
					<switch v-model="formData.pickup" class="swicth-item" :color="themeColor" />
				</form-item>
				<form-item label="送货上门" class="switch-form-item">
					<switch v-model="formData.delivery" class="swicth-item" :color="themeColor" />
				</form-item>
			</view>
			<form-item label="取货时间" arrow class="form-item pd-form-item input-item" @click.native="onClickPickupTime">
				<input type="text" v-model="formData.pickupTime" disabled placeholder="请选择" placeholder-class="placeholder" />
			</form-item>
			<form-item label="结算方式" arrow class="form-item pd-form-item input-item" @click.native="onClickSettlementWay">
				<input type="text" v-model="formData.settlementWayName" disabled placeholder="请选择" placeholder-class="placeholder" />
			</form-item>
			<form-item label="建议调度车型" arrow class="form-item pd-form-item input-item">
				<input type="text" v-model="formData.carType" disabled placeholder="请选择" placeholder-class="placeholder" />
			</form-item>
		</view>
		
		<view class="form-block">
			<form-item label="备注" arrow class="form-item input-item">
				<input type="text" v-model="formData.remark" placeholder="请输入" placeholder-class="placeholder" />
			</form-item>
		</view>
		
		<view class="action-pane">
			<cover-view class="amount-box">
				<cover-view class="amount-box-top">
					<cover-view class="label">预估：</cover-view>
					<cover-view class="amount">¥ 1000.00</cover-view>
				</cover-view>
				<cover-view class="amount-box-bottom">
					<cover-view>最终费用以实际为准</cover-view>
				</cover-view>
			</cover-view>
			<cover-view class="detail-info-button" @click="onDetailFeeClick">
				<cover-view>明细</cover-view>
				<cover-image  src="/static/img/icon_go-top@2x.png" mode="widthFix"></cover-image>
			</cover-view>
			<cover-view class="delivery-button">
				<cover-view>立即发货</cover-view>
			</cover-view>
		</view>
		
		<!-- 选择时间 -->
		<choose-time ref="chooseTimeRef" @confirm="onPickupTimeConfigm"></choose-time>
		
		<!-- 结算方式 -->
		<choose-settlement-way ref='chooseSettlementWayRef' @confirm="onSettlementWayConfirm" />
		
		<!-- 费用明细 -->
		<order-fee-detail ref='orderFeeDetailRef'></order-fee-detail>
		
	</view>
</template>

<script>
	import ImgUpload from '@/frame/components/img-upload/index.vue'
	import OrderAddressPicker from '@/components/order-parts/order-address-picker/index.vue'
	import FormItem from '@/components/form-item/index.vue'
	import NumberStep from '@/components/number-step/index.vue'
	import ChooseTime from './components/choose-time/index.vue'
	import ChooseSettlementWay from './components/choose-settlement-way/index.vue'
	import OrderFeeDetail from './components/order-fee-detail/index.vue'
	
	import { themeColor } from 'uni.scss'
	
	export default {
		components: {
			FormItem,
			OrderAddressPicker,
			ImgUpload,
			NumberStep,
			ChooseTime,
			ChooseSettlementWay,
			OrderFeeDetail
		},
		data() {
			return {
				themeColor,
				formData: {
					sendUserName: '张三',
					sendUserMobile: '138****8888',
					sendAddress: '浙江省宁波市江北某某某某某某某某某某某某某某某某某路',
					receiveUserName: '张三',
					receiveUserMobile: '138****8888',
					receiveAddress: '浙江省宁波市江北某某某某某某某某某某某某某某某某某路',
					img: '',
					number: 1,
					pickupTime: '',
					settlementWayName: '',
					settlementWay: null
				}
			}
		},
		methods: {
			// 选择取货时间
			onClickPickupTime() {
				this.$refs.chooseTimeRef.show(this.formData.pickupTime)
			},
			// 确定取货时间
			onPickupTimeConfigm(val) {
				this.formData.pickupTime = val
			},
			// 选择结算方式
			onClickSettlementWay() {
				this.$refs.chooseSettlementWayRef.show()
			},
			// 确定结算方式
			onSettlementWayConfirm(item) {
				this.formData.settlementWay = item.value
				this.formData.settlementWayName = item.label
			},
			// 点击明细
			onDetailFeeClick() {
				this.$refs.orderFeeDetailRef.show()
			},
			
			onChooseGoods() {
				uni.navigateTo({
					url: '/pages/goods-search-list/index'
				})
			}
		}
	}
</script>

<style lang="scss" scoped>
	.to-delivery-page {
		min-height: 100%;
		padding: 20upx 30upx 190upx;
		background-color: #F0F2F5;
	}
	
	.order-address-picker {
		display: block;
		border-radius: 16upx;
		padding: 0 30upx;
		background-color: #fff;
	}
	
	.form-block {
		background-color: #fff;
		padding: 30upx;
		border-radius: 16upx;
		margin-top: 30upx;
		font-size: 32upx;
		
		&.border-form-block {
			& > .form-item {
				border-top: 2upx solid $border-color;
				padding: 30upx 0;
				& + .form-item {
					margin-top: 0;
				}
			}
		}
	}
	
	.form-item {
		display: block;
		
		&.gap-form-item {
			& + .gap-form-item {
				margin-top: 30upx;
			}
		}
		
		&.pd-form-item {
			padding: 30upx 0;
			
			& + .pd-form-item {
				border-top: 2upx solid $border-color;
			}
		}
	}
	
	.goods-picker-input {
		flex-grow: 1;
		width: 0;
		text-align: right;
	}
	
	.number-input {
		width: 360upx;
		line-height: 72upx;
		height: 72upx;
		text-align: center;
		background-color: #F4F4F4;
		font-size: 32upx;
		border-radius: 10upx;
	}
	
	.switch-items {
		@include flex-c-center;
		padding-bottom: 30upx;
		border-bottom: 2upx solid $border-color;
		
		.switch-form-item {
			flex-grow: 1;
			flex-shrink: 0;
			
			.swicth-item {
				transform: scale(.7);
			}
			
			& + .switch-form-item {
				margin-left: 60upx;
				position: relative;
				
				&::before {
					content: '';
					position: absolute;
					left: -30upx;
					top: 0;
					height: 100%;
					background-color: #EFEFEF;
					width: 2upx;
					transform: scale(.7);
				}
			}
		}
	}
	
	.input-item {
		input {
			text-align: right;
			width: 100%;
			flex-grow: 1;
		}
	}
	
	.padding-bottom-zero {
		padding-bottom: 0;
	}
	
	.action-pane {
		position: fixed;
		left: 0;
		right: 0;
		bottom:0;
		background-color: #fff;
		box-shadow: 0px -4upx 20upx 0px rgba(0,0,0,0.0500), 0px 4px 20px 0px rgba(23,71,178,0.3800);
		@include flex;
		padding: 30upx;
		z-index: 97;
	}
	
	.amount-box {
		flex-shrink: 0;
		.amount-box-top {
			font-size: 28upx;
			@include flex;
			
			.amount {
				color: $theme-color;
				font-weight: 500;
			}
		}
		
		.amount-box-bottom {
			margin-top: 14upx;
			color: #A3A7B3;
			font-size: 24upx;
		}
	}
	
	.detail-info-button {
		padding-left: 30upx;
		border-left: 2upx solid $border-color;
		align-self: flex-start;
		margin-left: 30upx;
		flex-grow: 1;
		width: 0;
		@include flex-c-center;
		font-size: 28upx;
		font-weight: 500;
		
		cover-image {
			width: 28upx;
			height: 28upx;
			margin-left: 6upx;
		}
	}
	
	.delivery-button {
		background-color: $theme-color;
		color: #fff;
		line-height: 88upx;
		height: 88upx;
		text-align: center;
		padding: 0 60upx;
		border-radius: 44upx;
		font-size: 32upx;
		font-weight: 500;
		@include flex-center;
	}
	
</style>
